/* inter-regular - latin */
@font-face {
  font-display: swap;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  src: url('../fonts/inter/inter-v13-latin-regular.ttf') format('ttf'),
       url('../fonts/inter/inter-v13-latin-regular.woff') format('woff'),
       url('../fonts/inter/inter-v13-latin-regular.svg') format('svg'),
       url('../fonts/inter/inter-v13-latin-regular.woff2') format('woff2'); /* Chrome 36+, Opera 23+, Firefox 39+, Safari 12+, iOS 10+ */
}